<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="modbus"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '': 'nubeGreen'}`">
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="headers"
              :items="networks"
              class="elevation-1"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Modbus Networks</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left">
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="onClickRefresh">Refresh<v-icon x-small right>mdi-refresh</v-icon></v-btn>
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="nubeGreen" @click="onClickCreateEdit">Create<v-icon x-small right>mdi-plus</v-icon></v-btn>
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="error" @click="clickDeleteMulti" :disabled="selected.length === 0">Delete<v-icon x-small right>mdi-delete</v-icon></v-btn>
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small class="mr-1 btn-height-22 white--text" color="downloadColor" v-bind="attrs" v-on="on">Download<v-icon x-small right>mdi-download</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(networks, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" v-bind="attrs" v-on="on">Import<v-icon x-small right>mdi-import</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="exportColor" @click="clickJsonExport(selected)" :disabled="selected.length !== 1">Export<v-icon x-small right>mdi-export</v-icon></v-btn>
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="blue-grey" @click="onClickOpenDevicesView">View All Devices<v-icon x-small right>mdi-collage</v-icon></v-btn>
                    <v-btn x-small class="mr-1 btn-height-22 white--text" color="deep-purple" @click="onClickOpenPointsViewOfAllNetworks">View All Points<v-icon x-small right>mdi-view-headline</v-icon></v-btn>
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                  ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:[`item.name`]="{ item }">
                <a @click="onClickOpen(item)"> {{ item.name }} </a>
              </template>

              <template v-slot:[`item.enable`]="{ item }">
                <v-checkbox v-model="item.enable" @click="updateNetwork(item)"></v-checkbox>
              </template>

              <template v-slot:[`item.point_interval_ms_between_points`]="{ item }">
                <v-edit-dialog
                  :return-value.sync="item.point_interval_ms_between_points"
                  @save="updateNetwork(item)"
                  large
                >
                  <b>{{ item.point_interval_ms_between_points }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.point_interval_ms_between_points"
                      single-line
                      type="number"
                      min="0"
                      @input="val => (item.point_interval_ms_between_points = val ? Number(val) : 0)"
                    />
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:[`item.timeout`]="{ item }">
                <v-edit-dialog :return-value.sync="item.timeout" @save="updateNetwork(item)" large>
                  <b>{{ item.timeout }}</b>
                  <template v-slot:input>
                    <v-text-field
                      v-model="item.timeout"
                      single-line
                      type="number"
                      min="0"
                      @input="val => (item.timeout = val ? Number(val) : 0)"
                    />
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:[`item.actions`]="{ item }">
                <v-icon small @click="onClickCreateEdit(item)" color="primary">mdi-pencil</v-icon>
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>
                <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-icon v-bind="attrs" v-on="on" small @click="onClickOpenPointsView(item)"
                      >mdi-view-headline</v-icon
                    >
                  </template>
                  <span>View all points</span>
                </v-tooltip>
              </template>

              <template v-slot:[`header.enable`]>
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:[`header.point_interval_ms_between_points`]>
                Point Interval MS&nbsp;
                <v-icon x-small @click="openMassEditDialog('point_interval_ms_between_points')" color="primary"
                  >mdi-pencil</v-icon
                >&nbsp;
              </template>

              <template v-slot:[`header.timeout`]>
                {{ toUpperTextKey('timeout') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('timeout')" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server="modbus"></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- create/edit dialog -->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field
                    @input="changeName"
                    v-model="editedItem.name"
                    single-line
                    label="Network name"
                    required
                    :rules="[() => !isExistingvalue(editedItem, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.type"
                    label="Type"
                    :items="['RTU', 'TCP']"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="Enable" required></v-checkbox>
                </v-col>
                <!-- <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.history_enable" label="history enable"></v-checkbox>
                </v-col> -->
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.point_interval_ms_between_points"
                    label="delay between points (ms)"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="editedItem.timeout" label="timeout" type="number"></v-text-field>
                </v-col>
              </v-row>
              <v-row v-if="editedItem.type === 'TCP'">
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.tcp_ip"
                    label="ip"
                    required
                    :rules="[rules.required]"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.tcp_port"
                    label="port"
                    required
                    :rules="[rules.required]"
                    type="number"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row v-if="editedItem.type === 'RTU'">
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.rtu_parity"
                    label="rtu parity"
                    item-text="name"
                    item-value="value"
                    :items="rtu_parities"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select
                    v-model="editedItem.rtu_speed"
                    label="rtu speed"
                    :items="[9600, 19200, 38400, 57600, 115200]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_stop_bits" label="rtu stopbits" :items="[1, 1.5, 2]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-select v-model="editedItem.rtu_byte_size" label="rtu bytesize" :items="[5, 6, 7, 8]"></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-combobox
                    v-model="editedItem.rtu_port"
                    :items="rtu_ports"
                    label="rtu port"
                    :rules="[() => !isExistingvalue(editedItem, 'rtu_port') || 'Port Existed', rules.required]"
                  ></v-combobox>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="validWizard" ref="form_wizard" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
                :loading="savingImport"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> mdi-delete </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    >mdi-check-circle-outline</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="createNetwork(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    >mdi-cached</v-icon
                  >
                </template>
                <template v-slot:[`item.name`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                    v-model="item.name"
                    single-line
                    style="width: 100px"
                    disabled
                  ></v-text-field>
                  <v-text-field
                    v-else
                    v-model="item.name"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item, 'name') || 'Name Existed', rules.required]"
                  ></v-text-field>
                </template>
                <template v-slot:[`item.rtu_port`]="{ item }">
                  <div v-if="item.type == 'RTU'">
                    <v-text-field
                      v-if="isClickSaveImport && isExistingvalue(item, 'uuid')"
                      v-model="item.rtu_port"
                      single-line
                      disabled
                    ></v-text-field>
                    <v-combobox
                      v-else
                      v-model="item.rtu_port"
                      :items="rtu_ports"
                      :rules="[() => !isExistingvalue(item, 'rtu_port') || 'Port Existed', rules.required]"
                    ></v-combobox>
                  </div>
                </template>
              </v-data-table>
            </v-form>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="secondary" @click="e1 = 1">Back</v-btn>
            <v-btn x-small class="mr-1 btn-height-22 white--text" color="primary" @click="saveImport" :disabled="!validWizard || savingImport">Save</v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog
      :is-open="dialogImportExcel"
      :on-close="() => (dialogImportExcel = false)"
      :on-confirm="convertExcelToJson"
    ></import-from-excel-dialog>

    <!-- delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            min="0"
            v-model.number="editAll[editField]"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="val => (editAll[editField] = val ? Number(val) : 0)"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import * as modbusServerService from '../../services/modbusServerService';
import {
  RULES,
  TOPIC,
  NETWORK_HEADERS,
  NETWORK_RTU_PARITIES,
  NETWORK_RTU_PORTS,
  NETWORK_DEFAULT,
  NETWORK_EDIT_ALL_DEFAULT,
} from './modbusConstant';
import {
  copyToClipboard,
  isJsonString,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    MqttConsole,
    RightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
    SystemStatusTable,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    rules: { ...RULES },
    headers: [...NETWORK_HEADERS],
    rtu_parities: [...NETWORK_RTU_PARITIES],
    rtu_ports: [...NETWORK_RTU_PORTS],
    editedItem: { ...NETWORK_DEFAULT },
    editAll: { ...NETWORK_EDIT_ALL_DEFAULT },
    tableProps: { ...DATA_TABLE_PROPS },
    selected: [],
    networks: [],
    importArray: [],
    networksIncludeChild: [],
    action: '',
    dataImport: '',
    search: '',
    searchWizard: '',
    editField: '',
    dialog: false,
    dialogDelete: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    valid: false,
    validWizard: false,
    isClickSaveImport: false,
    savingImport: false,
    loadingData: false,
    tab: 0,
    editedIndex: -1,
    e1: 1,
    intervalId: null,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Network' : 'Edit Network';
    },
    pingData() {
      return this.$store.state.pingData;
    },
    networksData() {
      return this.$store.state.mbNetworks;
    },
    debugSubscriptions() {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    'dialog'(val) {
      val || this.close();
    },
    'dialogDelete'(val) {
      val || this.closeDelete();
    },
    '$store.state.pingData': function (val) {
      this.getPingData(val);
    },
  },
  beforeRouteLeave(to, from, next) {
    clearInterval(this.intervalId);
    this.$store.commit('showOverlay');
    axios.post(`mqtt/unsubscribe`, { topic: this.topic }).finally(() => {
      this.$store.commit('hideOverlay');

      next();
    });
  },
  mounted() {
    this.getNetworks();
    if (Object.keys(this.pingData).length !== 0) {
      this.getPingData(this.pingData);
    }
    //set auto refresh
    if (this.setting.refreshTime && this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(() => {
        this.getNetworks();
        if (this.setting.toastAutoRefresh) {
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getPingData(data) {
      if (Object.keys(data).length === 0) {
        this.$toasted.show('Point Server is not running', { type: 'error' });
        return;
      }
    },
    getNetworks() {
      this.loadingData = true;
      modbusServerService.fetchNetworks({ with_children: true }).then(data => {
        this.networks = data;
        this.$store.commit('changeMbNetworksData', data);
        this.loadingData = false;
      });
    },
    async getNetworkIncludeChild(item, resolve, reject) {
      try {
        const network = await modbusServerService.fetchNetwork(item.uuid, { with_children: true, point: true });
        this.networksIncludeChild.push(network);
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    onClickOpen(item) {
      this.$router.push({ path: `/modbus/networks/${item.uuid}/devices` });
    },
    onClickCreateEdit(item) {
      this.editedIndex = item && item.uuid ? this.networks.indexOf(item) : -1;
      this.editedItem = Object.assign({}, item && item.uuid ? item : NETWORK_DEFAULT);
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateNetwork(this.editedItem);
        } else {
          this.createNetwork(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
      this.selected = [];
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.networks.indexOf(this.editedItem);
      this.networks.splice(this.editedIndex, 1);
      await modbusServerService.deleteNetwork(this.editedItem.uuid);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, NETWORK_DEFAULT);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, NETWORK_DEFAULT);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form_wizard.validate()) {
          this.savingImport = true;
          this.isClickSaveImport = true;
          try {
            const promises = [];
            for (const item of this.importArray) {
              if (!item.uuid) {
                item.name = conventionNaming(item.name, this.setting);
                promises.push(this.createNetwork(item));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {
          } finally {
            this.savingImport = false;
          }
        }
      }
    },
    async clickImportJson() {
      this.networksIncludeChild = [];
      this.dialogImport = true;
      const promises = [];
      for (const item of this.selected) {
        promises.push(new Promise((resolve, reject) => this.getNetworkIncludeChild(item, resolve, reject)));
      }
      await Promise.all(promises);
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.networksIncludeChild) : '';
      this.e1 = 1;
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(nw => {
        delete nw.uuid;
        return nw;
      });
      this.e1 = 2;
    },
    isExistingvalue(item, fieldName) {
      if (!this.networks) return false;
      let value = item[fieldName];
      let networks = _cloneDeep(this.networks);
      if (this.editedIndex !== -1) {
        //when edit
        networks.splice(this.editedIndex, 1);
      }
      return networks.find(n => n[fieldName] == value) != null;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.devices = this.getDevicesArray(i);

            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    getDevicesArray(network) {
      let devices = [];
      let deviceIndexs = [];
      let deviceKeys = [];

      Object.keys(network).forEach(key => {
        //get deviceIndexs and deviceKeys
        let keys = key.split('.');
        let deviceKey = '';
        let deviceIndex = null;
        if (keys.length > 1) {
          deviceKey = keys[1];
          if (!deviceKeys.includes(deviceKey)) {
            deviceKeys.push(deviceKey);
          }
          deviceIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((deviceIndex || deviceIndex === 0) && !deviceIndexs.includes(deviceIndex)) {
            deviceIndexs.push(deviceIndex);
          }
        }
      });
      //get device
      deviceIndexs.forEach(i => {
        let device = {};
        deviceKeys.forEach(dk => {
          device = { ...device, [dk]: network[`device[${i}].${dk}`] };
        });
        //get points of device
        Object.keys(network).forEach(key => {
          let keys = key.split('.');
          if (keys[0] == `device[${i}]` && keys.length > 2) {
            device = { ...device, [`${keys[1]}.${keys[2]}`]: network[key] };
            device.points = this.getPointsArray(device);
          }
        });
        devices.push(device);
      });
      return devices;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      indexs.forEach(i => {
        let point = {};
        pointKeys.forEach(pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        points.push(point);
      });
      return points;
    },
    async clickJsonExport() {
      this.networksIncludeChild = [];
      const promises = [];
      for (const item of this.selected) {
        promises.push(new Promise((resolve, reject) => this.getNetworkIncludeChild(item, resolve, reject)));
      }
      await Promise.all(promises);
      const status = copyToClipboard(JSON.stringify(this.networksIncludeChild));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = this.convertExportData(array);
      if (type === 'csv') {
        return exportCSV('networks', data);
      }
      if (type === 'excel') {
        return exportExcel('networks', data);
      }
      if (type === 'json') {
        return exportJSON('networks', JSON.stringify(this.networks));
      }
    },
    convertExportData(array) {
      const data = _cloneDeep(array).map(network => {
        if (network.devices) {
          network.devices.forEach((device, indexD) => {
            //devices
            _forIn(device, (value, key) => {
              if (key != 'points') {
                const keyName = `device[${indexD}].${key}`;
                network = { ...network, [keyName]: value };
              } else {
                //points
                device.points.forEach((point, indexP) => {
                  _forIn(point, (point_value, point_key) => {
                    if (point_key != 'point_store') {
                      const pointKey = `device[${indexD}].point[${indexP}].${point_key}`;
                      network = { ...network, [pointKey]: point_value };
                    } else {
                      //point_store
                      _forIn(point.point_store, (point_store_value, point_store_key) => {
                        const pointStoreKey = `device[${indexD}].point[${indexP}].point_store.${point_store_key}`;
                        network = { ...network, [pointStoreKey]: point_store_value };
                      });
                      delete point.point_store;
                    }
                  });
                });
                delete device.points;
              }
            });
          });
        }
        delete network.devices;
        return network;
      });
      return data;
    },
    updateNetwork(data) {
      modbusServerService.updateNetwork(data.uuid, data).then(response => {
        const index = this.networks.findIndex(n => n.uuid == response.uuid);
        Object.assign(this.networks[index], response);
        this.updateMbNetworksData(data);
        this.close();
        this.$toasted.show('update success');
      });
    },
    async createNetwork(data) {
      if (data.type == 'TCP') {
        delete data.rtu_parity;
        delete data.rtu_speed;
        delete data.rtu_stop_bits;
        delete data.rtu_byte_size;
        delete data.rtu_port;
      }
      modbusServerService.createNetwork(data).then(response => {
        this.networks.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if (index > -1) {
            Object.assign(this.importArray[index], response);
          }
        }
        if (data.devices && data.devices.length > 0) {
          for (const device of data.devices) {
            device.network_uuid = response.uuid;
            device.address = device.address ? device.address : 0;
            this.createDevice(device);
          }
        }
        this.updateMbNetworksData(data);
        this.close();
        this.$toasted.show('create success');
      });
    },
    updateMbNetworksData(newNetwork) {
      const indexNetWork = this.networksData.findIndex(nw => nw.uuid == newNetwork.uuid);
      if (indexNetWork > -1) {
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeMbNetworksData', this.networksData);
      }
    },
    createDevice(data) {
      modbusServerService.createDevice(data).then(response => {
        if (data.points && data.points.length > 0) {
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            this.createPoint(point);
          }
        }
      });
    },
    createPoint(data) {
      modbusServerService.createPoint(data);
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'nubeYellow';
      } else {
        classColor = !item.enable ? 'nubeLightGrey' : '';
      }
      return classColor;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateNetwork(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { ...NETWORK_EDIT_ALL_DEFAULT };
    },
    onClickOpenPointsView(network) {
      this.$router.push({ path: `/modbus/networks/${network.uuid}/points-view` });
    },
    onClickRefresh() {
      this.getNetworks();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onClickOpenDevicesView() {
      this.$router.push({ path: `/modbus/networks/devices-view` });
    },
    onClickOpenPointsViewOfAllNetworks() {
      this.$router.push({ path: `/modbus/networks/all-networks-points-view` });
    },
    changeName(val) {
      this.editedItem.name = conventionNaming(val, this.setting);
    },
  },
};
</script>

